.Sidebar {
  height: 100%;
  width: 15%;
  background: linear-gradient(90deg, rgb(39, 39, 39) 0%, rgb(93, 92, 93) 100%);
  padding-top: 0px;
  position: fixed;
}
.SidebarList {
  height: auto;
  padding: 0;
  width: 100%;
}

.SidebarList .row {
  width: 100%;
  height: 50px;
  list-style-type: none;
  margin: 0%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  color: white;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  font-weight: bold;
}

.SidebarList .row:hover {
  cursor: pointer;
  background-color: white;
  color: black;
}

.SidebarList #active {
  background-color: white;
  color: black;
}

.row #icon {
  -webkit-flex: 30% 1;
          flex: 30% 1;
  display: grid;
  place-items: center;
}
.row #title {
  -webkit-flex: 70% 1;
          flex: 70% 1;
}

